import { StateCreator } from 'zustand';
import exampleImage from '../../assets/image/demo.png';
let filetype = 'image/png';

interface ImageConfigParam {
  blockSize: number;
  weight: number;
  iters: number;
  stride: number;
  withGrid: boolean;
  withDot: boolean;
  withCenters: boolean;
  withContours: boolean;
}

const defaultImageParam = {
  blockSize: 20,
  weight: 30,
  iters: 10,
  stride: 10,
  withGrid: true,
  withDot: false,
  withCenters: false,
  withContours: false,
};

export interface ImageConfigSlice {
  imageConfig: ImageConfigParam;
  exportImageTS: number;
  imageURL: string;
  imageFileType: string;
  setImageConfig: (options: any) => void;
  setExportImageTS: (exportImageTS: number) => void;
  setImageURL: (imageURL: string) => void;
  setImageFileType: (imageFileType: string) => void;
};

export const createImageConfigSlice: StateCreator<ImageConfigSlice> = (set) => ({
  imageConfig: defaultImageParam,
  exportImageTS: 0,
  imageURL: exampleImage,
  imageFileType: filetype,
  setImageConfig: (options) => (set(state => ({ imageConfig: { ...state.imageConfig, ...options } }))),
  setExportImageTS: (exportImageTS) => (set({ exportImageTS })),
  setImageURL: (imageURL) => (set({ imageURL })),
  setImageFileType: (imageFileType) => (set({ imageFileType })),
});
