import * as React from 'react';
import ScrollView from 'react-custom-scrollbars';
import classnames from 'classnames';
import styles from './index.less';

export interface MScrollViewProps {
  autoHeight?: boolean;
  children?: React.ReactNode;
  onScroll?: (event: React.UIEvent<any>) => void;
  classname?: string;
}

const MScrollView = (props: MScrollViewProps) => {
  const {
    children,
    onScroll,
    autoHeight,
    classname,
  } = props;
  return (
    <div className={classnames(styles.container, autoHeight && styles.autoHeight)}>
      <ScrollView autoHide onScroll={onScroll} className={classname}>
        {children}
      </ScrollView>
    </div>
  );
};

export default MScrollView;
