import React, { useEffect, useState, useRef } from 'react';
import classnames from 'classnames';
import { Space, Button, Input, Spin } from 'antd';
import { MinusOutlined, PlusOutlined, RetweetOutlined } from '@ant-design/icons';
import { useImageConfigStore } from '@/stores';
import SLIC from '@/utils/slic';
import styles from './index.less';

let pixelImage: any;
let slic: any;

const scaleList = [25, 33, 50, 67, 75, 80, 90, 100]; // , 110, 125, 150, 175, 200, 250, 300, 400, 500];
const defaultScaleIndex = 7;

const Preview = () => {
  const [initLoading, setInitLoading] = useState(true);
  const [loading, setLoading] = useState(false);
  const [scaleIndex, setScaleIndex] = useState(defaultScaleIndex);
  const imageConfig = useImageConfigStore((state) => state.imageConfig);
  const exportImageTS = useImageConfigStore((state) => state.exportImageTS);
  const imageURL = useImageConfigStore((state) => state.imageURL);
  const imageFileType = useImageConfigStore((state) => state.imageFileType);
  const setImageConfig = useImageConfigStore((state) => state.setImageConfig);
  const previewRef = useRef<HTMLCanvasElement>(null);
  const centersRef = useRef<HTMLCanvasElement>(null);
  const contoursRef = useRef<HTMLCanvasElement>(null);

  // 处理图片
  const dealImg = async (img: HTMLImageElement) => {
    const previewCanvas = previewRef.current!;
    const centersCanvas = centersRef.current!;
    const contoursCanvas = contoursRef.current!;
    const previewContext = previewRef.current!.getContext('2d');

    // 处理三层容器的尺寸
    previewCanvas.width = img.width;
    previewCanvas.height = img.height;
    centersCanvas.width = img.width;
    centersCanvas.height = img.height;
    contoursCanvas.width = img.width;
    contoursCanvas.height = img.height;

    // 绘制原始图片
    previewContext!.drawImage(img, 0, 0, previewCanvas.width, previewCanvas.height);
    // // 获取对应点数据
    const binaryData = previewContext!.getImageData(0, 0, previewCanvas.width, previewCanvas.height).data;
    // 计算每一个像素尺寸
    const superpixels = 500;
    const blockSize = Math.round(Math.sqrt((previewCanvas.width * previewCanvas.height) / superpixels));
    setImageConfig({ blockSize });
    slic = new SLIC(binaryData, previewCanvas.width, previewCanvas.height);
    // 生成像素图数据
    pixelImage = slic.pixelDeal(blockSize, imageConfig.iters, imageConfig.stride, imageConfig.weight);
    // 绘制像素图
    drawImage();
  };

  // 绘制图片
  const drawImage = () => {
    const previewCanvas = previewRef.current!;
    const previewContext = previewRef.current!.getContext('2d');
    // 绘制像素图
    const pixeArr = previewContext!.createImageData(previewCanvas.width, previewCanvas.height);
    for (let i = 0; i < pixelImage.length; i++) {
      pixeArr.data[i] = pixelImage[i];
    }
    previewContext!.putImageData(pixeArr, 0, 0);

    // 绘制格子
    if (imageConfig.withGrid) {
      if (imageConfig.withDot) {
        // 绘制镂空圆点 TODO
        for (let i = 0; i < previewCanvas.height + imageConfig.stride; i += imageConfig.stride) {
          for (let j = 0; j < previewCanvas.width + imageConfig.stride; j += imageConfig.stride) {
            const p = (j + i * previewCanvas.width) * 4;
            previewContext!.fillStyle = `rgba(${pixelImage[p]}, ${pixelImage[p + 1]}, ${pixelImage[p + 2]}, ${pixelImage[p + 3]})`;
            previewContext!.fillRect(j, i, imageConfig.stride, imageConfig.stride);
            previewContext!.beginPath();
            previewContext!.arc(j, i, imageConfig.stride / 4, 0, 2 * Math.PI);
            // previewContext.strokeStyle = `rgba(${pixelImage[p]}, ${pixelImage[p + 1]}, ${pixelImage[p + 2]}, ${pixelImage[p + 3]})`;
            previewContext!.fillStyle = '#000';
            previewContext!.fill();
            previewContext!.stroke();
            // previewContext.stroke();
          }
        }
      }
      previewContext!.strokeStyle = '#000';
      previewContext!.lineWidth = 2;
      for (let i = 0; i < previewCanvas.width + imageConfig.stride; i += imageConfig.stride) {
        previewContext!.beginPath();
        previewContext!.moveTo(i, 0);
        previewContext!.lineTo(i, previewCanvas.height);
        previewContext!.stroke();
      }
      for (let i = 0; i < previewCanvas.height + imageConfig.stride; i += imageConfig.stride) {
        previewContext!.beginPath();
        previewContext!.moveTo(0, i);
        previewContext!.lineTo(previewCanvas.width, i);
        previewContext!.stroke();
      }
    }
  };

  // 聚类中心
  const drawCenters = () => {
    if (imageConfig.withCenters) {
      const centersContext = centersRef.current!.getContext('2d');
      slic.showCenters(centersContext);
    }
  };

  // 边缘轮廓
  const drawContours = () => {
    if (imageConfig.withContours) {
      const contoursContext = contoursRef.current!.getContext('2d');
      slic.showContours(contoursContext);
    }
  };

  const toDraw = (fn: () => void) => {
    if (pixelImage && slic) {
      fn();
      drawImage();
      drawCenters();
      drawContours();
    }
  };

  // 更换图片
  useEffect(() => {
    setLoading(true);
    handleScale('reset');
    const img = new Image();
    img.src = imageURL;
    img.onload = () => {
      if (initLoading) {
        setInitLoading(false);
      }
      setLoading(false);
      dealImg(img);
    };
  }, [imageURL]);

  // 超像素点大小
  useEffect(() => toDraw(() => (pixelImage = slic.changeBlockSize(Math.round(imageConfig.blockSize)))), [imageConfig.blockSize]);
  // 取像素点步长
  useEffect(() => toDraw(() => (pixelImage = slic.changeStride(Math.round(imageConfig.stride)))), [imageConfig.stride]);
  // 算法迭代次数
  useEffect(() => toDraw(() => (pixelImage = slic.changeIters(Math.round(imageConfig.iters)))), [imageConfig.iters]);
  // 颜色空间权重
  useEffect(() => toDraw(() => (pixelImage = slic.changeWeight(Math.round(imageConfig.weight)))), [imageConfig.weight]);
  // 像素网格
  useEffect(() => pixelImage && drawImage(), [imageConfig.withGrid]);
  // // 像素圆点
  useEffect(() => pixelImage && drawImage(), [imageConfig.withDot]);
  // 聚类中心
  useEffect(() => drawCenters(), [imageConfig.withCenters]);
  // 边缘轮廓
  useEffect(() => drawContours(), [imageConfig.withContours]);
  // 导出图片
  useEffect(() => {
    if (exportImageTS) {
      const previewCanvas = previewRef.current!;
      const imgURL = previewCanvas.toDataURL(imageFileType);
      const link = document.createElement('a');
      link.download = `${exportImageTS}`;
      link.href = imgURL;
      link.dataset.downloadurl = [imageFileType, link.download, link.href].join(':');
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  }, [exportImageTS]);

  // 修改缩放
  const handleScale = (type: 'zoomIn' | 'zoomOut' | 'reset') => {
    if (type === 'zoomIn' && scaleIndex < scaleList.length - 1) {
      setScaleIndex(scaleIndex + 1);
    }
    if (type === 'zoomOut' && scaleIndex > 0) {
      setScaleIndex(scaleIndex - 1);
    }
    if (type === 'reset') {
      setScaleIndex(7);
    }
  };
  return (
    <div className={styles.container}>
      <div className={styles.header}>
        <Space.Compact block className={styles.scale}>
          <Button icon={<MinusOutlined />} onClick={() => handleScale('zoomOut')} />
          <Input style={{ width: 70, textAlign: 'center' }} value={`${scaleList[scaleIndex]}%`} />
          <Button icon={<PlusOutlined />} onClick={() => handleScale('zoomIn')} />
          <Button icon={<RetweetOutlined />} onClick={() => handleScale('reset')} />
        </Space.Compact>
      </div>
      <div className={styles.content}>
        <Spin spinning={loading}>
          <div className={classnames(styles.panel, initLoading && styles.hidden)} style={{ transform: `scale(${scaleList[scaleIndex]}%)` }}>
            <canvas ref={previewRef} className={styles.preview} />
            <canvas ref={centersRef} className={classnames(styles.centers, !imageConfig.withCenters && styles.hidden)} />
            <canvas ref={contoursRef} className={classnames(styles.contours, !imageConfig.withContours && styles.hidden)} />
          </div>
        </Spin>
      </div>
    </div>
  );
};

export default Preview;
