import React from 'react';
import classnames from 'classnames';
import styles from './index.less';

function Layout({
  main,
  icon,
  menu,
  copyright,
  reverse = false,
}: any) {
  return (
    <div className={classnames(styles.layout, reverse && styles.reverse)}>
      <div className={styles.mainContainer}>{main}</div>
      <div className={styles.miniContainer}>
        <div className={styles.iconWrap}>{icon}</div>
        <div className={styles.menuWrap}>{menu}</div>
        {copyright && <div className={styles.copyrightWrap}>{copyright}</div>}
      </div>
    </div>
  );
}

export default Layout;
