import React, { useState, useEffect } from 'react';
import classnames from 'classnames';
import { Button, Slider, Switch, Tooltip, Upload } from 'antd';
import { QuestionCircleOutlined } from '@ant-design/icons';
import ScrollView from '@/components/ScrollView';
import { useImageConfigStore } from '@/stores';
import styles from './index.less';

const options = [
  { title: '超像素点大小', key: 'blockSize', info: '值越小，超像素点分割越细腻', type: 'number', min: 10, max: 30 },
  { title: '取像素点步长', key: 'stride', info: '值越小，生成像素图越接近超像素点', type: 'number', min: 5, max: 20 },
  { title: '算法迭代次数', key: 'iters', info: '值越大，结果越精准，计算时间越长', type: 'number', min: 5, max: 20 },
  { title: '颜色空间权重', key: 'weight', info: '值越大，颜色对分割结果的影响越大', type: 'number', min: 1, max: 40 },
  { title: '像素网格', key: 'withGrid', type: 'boolean' },
  { title: '像素圆点', key: 'withDot', type: 'boolean' },
  { title: '聚类中心', key: 'withCenters', type: 'boolean' },
  { title: '边缘轮廓', key: 'withContours', type: 'boolean' },
];

const Control = () => {
  const imageConfig = useImageConfigStore((state) => state.imageConfig);
  const [state, setState] = useState({
    blockSize: imageConfig.blockSize,
    stride: imageConfig.stride,
    iters: imageConfig.iters,
    weight: imageConfig.weight,
  });
  const setImageConfig = useImageConfigStore((state) => state.setImageConfig);
  const setExportImageTS = useImageConfigStore((state) => state.setExportImageTS);
  const setImageURL = useImageConfigStore((state) => state.setImageURL);
  const setImageFileType = useImageConfigStore((state) => state.setImageFileType);
  const updateImageConfig = (key: string, value: number | boolean) => {
    const options: any = {};
    options[key] = value;
    console.log('options', options);
    setImageConfig(options);
  };
  const handleExportImage = () => (setExportImageTS(Date.now()));

  const UploadProps: any = {
    showUploadList: false,
    accept: '.png, .jpeg, .jpg, .webp',
    beforeUpload: (file: any) => {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
          setImageFileType(file.type);
          setImageURL(reader.result as string);
          reject();
        };
      });
    },
  };
  useEffect(() => {
    if (imageConfig.blockSize !== state.blockSize) {
      setState({ ...state, blockSize: imageConfig.blockSize });
    }
  }, [imageConfig.blockSize]);
  return (
    <div className={styles.container}>
    <ScrollView autoHeight>
      <div className={styles.panel}>
        {options.map(item => (
          <div className={classnames(styles.item, item.type === 'boolean' && styles.w50)} key={item.key}>
            <div className={styles.title}>
              <span>{item.title}</span>
              {item.info && (<Tooltip title={item.info}><QuestionCircleOutlined className={styles.question} /></Tooltip>)}
            </div>
            <div className={styles.action}>
              {item.type === 'number' && (
                <Slider
                  keyboard={false}
                  min={item.min}
                  max={item.max}
                  // value={(imageConfig as any)[item.key]}
                  value={(state as any)[item.key]}
                  onChange={(value) => setState({ ...state, ...{ [item.key]: value } })}
                  onChangeComplete={(value) => updateImageConfig(item.key, value)}
                />
              )}
              {item.type === 'boolean' && (
                <Switch
                  checkedChildren="开启"
                  unCheckedChildren="关闭"
                  checked={(imageConfig as any)[item.key]}
                  onChange={(checked) => updateImageConfig(item.key, checked)}
                />
              )}
            </div>
          </div>
        ))}
        <div className={classnames(styles.item, styles.btns)}>
          <Upload {...UploadProps}>
            <Button type="primary">上传图片</Button>
          </Upload>
          <Button onClick={handleExportImage}>导出图片</Button>
        </div>
      </div>
    </ScrollView >
  </div >
  );
};

export default Control;
